.app__businesstype {
    position: relative;
}

.app__businesstype-overlay{
    position: absolute;
    width: 15px;
    inset: 0;
}

.app__businesstype-overlay img{
    width: 15px;
    z-index: 0;
    margin: 4rem 6rem;
}   

.app__businesstype-content{
    width: 100%;
    z-index: 2;
}
.app__businesstype-content_about{
    flex: 1;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;

    text-align: right;
}
.app__businesstype-content_knife{
    margin: 2rem 7rem;
}
.app__businesstype-content_knife img{
    width:70%;
}
.app__businesstype-content_history{
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;

    text-align: left;
}

.app__businesstype-content_about p,
.app__businesstype-content_history p{
    margin: 2rem 0;
    color: var(--color-grey);
}

@media screen and (max-width:2000px) {
    .app__businesstype-content_knife img{
        height: 900px;
    }
    .app__businesstype-content_about p,
    .app__businesstype-content_history p{
    margin: 4rem 0;
    color: var(--color-grey);
    }
}

@media screen and (max-width:900px) {
    .app__businesstype-content{
        flex-direction: column;
    }
    .app__businesstype-content_knife{
        margin: 4rem 0;
    }
}

@media screen and (max-width:650px) {
    .app__businesstype-overlay img{
        width: 30px;
    }
}   