.app__header {
    background-color: var(--color-black);
}
.app__header-h1{
    font-family: var(--font-base);
    color: var(--color-green);
    letter-spacing: 0.04em;
    line-height: 117px;
    text-transform: uppercase;
    font-size: 90px;
}

.app__header-img img{
    width: 100%;
    color: var(--color-green);
    
}
.app__wrapper_img img{
    border: 5px solid;
    border-color: var(--color-green);
}